import React, {useState } from 'react';
import { MDBBtn, MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBCol } from 'mdbreact';
import '../components/card.css';
const CardExample = ({header,type,ids}) => {
  
  const[losses,setLosses]=useState("")
  const[wins,setWins]=useState("")
  const[headshotKills,setHeadshotKills]=useState("")
  const[kills,setkills]=useState("")
  const[longestKill,setLongestKill]=useState("")
  const[checks,setChecks]=useState("")
  
  let datas={
    headers:{
     
      'Authorization':'Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJqdGkiOiI1MmM2MTgxMC00NTU2LTAxMzktZmE4OS0wZDExNzBkZGYxZjMiLCJpc3MiOiJnYW1lbG9ja2VyIiwiaWF0IjoxNjEyMDMxMDczLCJwdWIiOiJibHVlaG9sZSIsInRpdGxlIjoicHViZyIsImFwcCI6Ii00ZDVhZmY3Zi05NjFkLTQxMzQtOGQ2Yi05NGE2NWRkODg4NmMifQ.z2JrSIK182bWeb9m_C91Q--g5UxvIMN4Z3VPBQdDE4U',
      'Content-Type':'application/json',
      'X-Requested-With': 'XMLHttpRequest',
      "Accept": "application/vnd.api+json"
      
      
    
    },
   

  }
  const findingSoloStatus=(event)=>{
   event.preventDefault()
    fetch(`https://cors-anywhere.herokuapp.com/https://api.pubg.com/shards/steam/seasons/lifetime/gameMode/solo/players?filter[playerIds]=${ids}&filter[gamepad]=true`,datas)
    .then(response =>response.json()
    ).then((result) => {
      
      setHeadshotKills(result.data[0].attributes.gameModeStats.solo.headshotKills)
      setWins(result.data[0].attributes.gameModeStats.solo.wins)
      setLosses(result.data[0].attributes.gameModeStats.solo.losses)
      setkills(result.data[0].attributes.gameModeStats.solo.kills)
      setLongestKill(result.data[0].attributes.gameModeStats.solo.longestKill)
      setChecks(result.data[0].type)
      
    }
    ).catch((err)=>{if(err){
     console.log(err);
    }})
  }
  

const findingDuoStatus=(event)=>{
 event.preventDefault()
  fetch(`https://cors-anywhere.herokuapp.com/https://api.pubg.com/shards/steam/seasons/lifetime/gameMode/duo/players?filter[playerIds]=${ids}&filter[gamepad]=true`,datas)
  .then(response =>response.json()
  ).then((result) => {
   if(result){
    
    setHeadshotKills(result.data[0].attributes.gameModeStats.duo.headshotKills)
    setWins(result.data[0].attributes.gameModeStats.duo.wins)
    setLosses(result.data[0].attributes.gameModeStats.duo.losses)
    setkills(result.data[0].attributes.gameModeStats.duo.kills)
    setLongestKill(result.data[0].attributes.gameModeStats.duo.longestKill)
    setChecks(result.data[0].type)
   
   }
 
   
   
  }
  ).catch((err)=>{if(err){
   console.log(err);
  }})
}


const findingSquadStatus=(event)=>{
 event.preventDefault()
  fetch(`https://cors-anywhere.herokuapp.com/https://api.pubg.com/shards/steam/seasons/lifetime/gameMode/squad/players?filter[playerIds]=${ids}&filter[gamepad]=true`,datas)
  .then(response =>response.json()
  ).then((result) => {
    if(result){
      
    setHeadshotKills(result.data[0].attributes.gameModeStats.squad.headshotKills)
    setWins(result.data[0].attributes.gameModeStats.squad.wins)
    setLosses(result.data[0].attributes.gameModeStats.squad.losses)
    setkills(result.data[0].attributes.gameModeStats.squad.kills)
    setLongestKill(result.data[0].attributes.gameModeStats.squad.longestKill)
    setChecks(result.data[0].type)
   
    }


  }
  ).catch((err)=>{if(err){
   console.log(err);
  }})
}

  return !header ?( <div></div>):(!checks ?(<MDBCol style={{ maxWidth: "22rem" }} className="mb-5">
  <MDBCard className="mb-5" id="mb" >
    <MDBCardImage id="image" className="img-fluid" src="data:image/jpeg;base64,
"
       />
    <MDBCardBody id="body" >
     
      <MDBCardTitle id="text">Name : {header}</MDBCardTitle>
      <MDBCardText  id="text">Mode : {type}</MDBCardText>
      <MDBCardText  id="text">Player ID : {ids}</MDBCardText>
     
      <MDBBtn color ="gold"  id="bu" href="#" onClick={findingSoloStatus}>SOLO</MDBBtn>
      <MDBBtn color ="gold" id="bu" href="#" onClick={findingDuoStatus}>DUO</MDBBtn>
      <MDBBtn color ="gold" id="bu" href="#" onClick={findingSquadStatus}>SQUAD</MDBBtn>
    
    </MDBCardBody>
  </MDBCard>
</MDBCol>):(
    <MDBCol style={{ maxWidth: "22rem" }} className="mb-5">
      <MDBCard className="mb-5" id="mb" >
        <MDBCardImage id="image" className="img-fluid" src="data:image/jpeg;base64,
"
           />
        <MDBCardBody  id="body" >
         
          <MDBCardTitle id="text">Name : {header}</MDBCardTitle>
          <MDBCardText  id="text">Mode : {type}</MDBCardText>
          <MDBCardText  id="text">Player ID : {ids}</MDBCardText>
         
          <MDBBtn color ="gold" id="bu" href="#" onClick={findingSoloStatus}>SOLO</MDBBtn>
          <MDBBtn color ="gold" id="bu" href="#" onClick={findingDuoStatus}>DUO</MDBBtn>
          <MDBBtn color ="gold" id="bu" href="#" onClick={findingSquadStatus}>SQUAD</MDBBtn>
         
          
          <div className="mt-3">
           <MDBCardTitle id="text">Wins : {wins}</MDBCardTitle>
           <MDBCardTitle id="text">Losses : {losses}</MDBCardTitle>
           <MDBCardTitle id="text">Kills : {kills}</MDBCardTitle>
           <MDBCardTitle id="text">Head Shot Kills : {headshotKills}</MDBCardTitle>
           <MDBCardTitle id="text">Longest Kills : {longestKill}</MDBCardTitle>
          </div>
         
          

  
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  )
  
  )
}

export default CardExample;